"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { Sidebar } from "@/components/layout/sidebar"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { ChevronLeft, Plus, Save, Trash2 } from "lucide-react"

export default function NewReceiptPage() {
  const router = useRouter()
  const [warehouses, setWarehouses] = useState<any[]>([])
  const [products, setProducts] = useState<any[]>([])
  
  // Form State
  const [supplier, setSupplier] = useState("")
  const [warehouse, setWarehouse] = useState("")
  const [lines, setLines] = useState<{product: string, quantity: number}[]>([])

  // Fetch Master Data
  useEffect(() => {
    const fetchData = async () => {
      const token = localStorage.getItem("token")
      const headers = { Authorization: `Token ${token}` }
      
      const [whRes, prodRes] = await Promise.all([
        fetch("http://127.0.0.1:8000/api/warehouses/", { headers }),
        fetch("http://127.0.0.1:8000/api/products/", { headers })
      ])

      if (whRes.ok) setWarehouses(await whRes.json())
      if (prodRes.ok) setProducts(await prodRes.json())
    }
    fetchData()
  }, [])

  const addLine = () => {
    setLines([...lines, { product: "", quantity: 1 }])
  }

  const updateLine = (index: number, field: string, value: any) => {
    const newLines = [...lines]
    newLines[index] = { ...newLines[index], [field]: value }
    setLines(newLines)
  }

  const handleSubmit = async () => {
    const token = localStorage.getItem("token")
    
    // 1. Create Receipt Header
    const res = await fetch("http://127.0.0.1:8000/api/receipts/", {
      method: "POST",
      headers: { 
        "Content-Type": "application/json",
        Authorization: `Token ${token}` 
      },
      body: JSON.stringify({ supplier, warehouse, status: "draft" })
    })

    if (!res.ok) {
      alert("Failed to create receipt header")
      return
    }

    const receipt = await res.json()

    // 2. Create Receipt Lines
    for (const line of lines) {
      await fetch("http://127.0.0.1:8000/api/receipt-items/", {
        method: "POST",
        headers: { 
          "Content-Type": "application/json",
          Authorization: `Token ${token}` 
        },
        body: JSON.stringify({
          receipt: receipt.id,
          product: line.product,
          quantity: line.quantity
        })
      })
    }

    // 3. Validate (Optional: Auto-validate for hackathon speed)
    const validateRes = await fetch(`http://127.0.0.1:8000/api/receipts/${receipt.id}/validate/`, {
        method: "POST",
        headers: { Authorization: `Token ${token}` }
    })

    if(validateRes.ok) {
        alert("Receipt Created & Validated! Stock updated.")
        router.push("/products") // Go back to see the new stock!
    }
  }

  return (
    <div className="flex min-h-screen bg-background">
      <Sidebar />
      <main className="flex-1 ml-64 p-8">
        <div className="flex items-center gap-4 mb-8">
          <Button variant="ghost" size="icon" onClick={() => router.back()}>
            <ChevronLeft className="h-4 w-4" />
          </Button>
          <h1 className="text-3xl font-bold">Receive Goods</h1>
        </div>

        <div className="grid gap-6 max-w-4xl mx-auto">
          <Card>
            <CardHeader><CardTitle>General Info</CardTitle></CardHeader>
            <CardContent className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label>Supplier</Label>
                <Input 
                  placeholder="e.g. Acme Corp" 
                  value={supplier} 
                  onChange={e => setSupplier(e.target.value)} 
                />
              </div>
              <div className="space-y-2">
                <Label>Destination Warehouse</Label>
                <Select onValueChange={setWarehouse}>
                  <SelectTrigger><SelectValue placeholder="Select..." /></SelectTrigger>
                  <SelectContent>
                    {warehouses.map(w => (
                      <SelectItem key={w.id} value={w.id.toString()}>{w.name}</SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between">
              <CardTitle>Products</CardTitle>
              <Button size="sm" onClick={addLine}><Plus className="h-4 w-4 mr-2"/> Add Line</Button>
            </CardHeader>
            <CardContent>
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Product</TableHead>
                    <TableHead className="w-[100px]">Qty</TableHead>
                    <TableHead className="w-[50px]"></TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {lines.map((line, index) => (
                    <TableRow key={index}>
                      <TableCell>
                        <Select onValueChange={(v) => updateLine(index, 'product', v)}>
                          <SelectTrigger><SelectValue placeholder="Select Product" /></SelectTrigger>
                          <SelectContent>
                            {products.map(p => (
                              <SelectItem key={p.id} value={p.id.toString()}>{p.name}</SelectItem>
                            ))}
                          </SelectContent>
                        </Select>
                      </TableCell>
                      <TableCell>
                        <Input 
                          type="number" 
                          value={line.quantity} 
                          onChange={(e) => updateLine(index, 'quantity', parseFloat(e.target.value))}
                        />
                      </TableCell>
                      <TableCell>
                        <Button variant="ghost" size="icon" className="text-red-500">
                          <Trash2 className="h-4 w-4" />
                        </Button>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </CardContent>
          </Card>

          <div className="flex justify-end">
            <Button size="lg" onClick={handleSubmit} className="bg-green-600 hover:bg-green-700">
              <Save className="mr-2 h-4 w-4" /> Validate Receipt
            </Button>
          </div>
        </div>
      </main>
    </div>
  )
}